import { INIT_URL, GET_ALL_PATIENTS, GET_CONVO_LIST, CARE_PLAN_FOR_PATIENT, CONVO_PATIENT_ADMIN, SUGGESTIONS_PATIENT_ADMIN } from "../../constants/ActionTypes";

const INIT_STATE = {

};

export default (state = INIT_STATE, action) => {
    switch (action.type) {


        case INIT_URL: {
            return { ...state, initURL: action.payload };
        }
        case GET_ALL_PATIENTS: {
            return { ...state, patients: action.payload };
        }
        case GET_CONVO_LIST: {
            return { ...state, convoList: action.payload };
        }

        case CARE_PLAN_FOR_PATIENT: {
            return { ...state, plan: action.payload };
        }
        case CONVO_PATIENT_ADMIN: {
            return { ...state, convo: action.payload };
        }
        case SUGGESTIONS_PATIENT_ADMIN: {
            return { ...state, suggestions: action.payload };
        }


        default:
            return state;
    }
}
