import React from "react";
import { Route, Switch } from "react-router-dom";

import asyncComponent from "util/asyncComponent";

const App = ({ match }) => (
  <div className="gx-main-content-wrapper">
    <Switch>
      <Route path={`${match.url}sample`} component={asyncComponent(() => import('./SamplePage'))} />
      <Route path={`${match.url}addpatient`} component={asyncComponent(() => import('./SamplePage/Addpatient'))} />
      <Route path={`${match.url}patientlist`} component={asyncComponent(() => import('./SamplePage/PatientList'))} />
      <Route path={`${match.url}messages`} component={asyncComponent(() => import('./SamplePage/Messages'))} />

    </Switch>
  </div>
);

export default App;
